*{
  box-sizing: border-box;
  margin: 0;
}

#root {
  position: fixed;
  overflow: hidden;
  width: 100svw;
  height: 100svh;
}

html,body {
  overscroll-behavior-y: contain;
  margin: 0;
  padding: 0;
  user-select: none;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial,
      sans-serif;
  position: fixed;
  overflow: hidden;
  max-height: 100svh;
  height: 100svh;
  width: 100svw;
  max-width: 100svw;
}

.App {
  display: flex;
  flex-direction: column;
  min-width: 100svw;
  min-height: 100svh;
  max-width: 100svw;   
  max-height: 100svh;
  text-align: center;
  transform: scale(1);
  position: absolute;
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(154, 154, 154);
}

.entrez {
  color: #61dafb;
}
