.logoZen{
  width: 25%;
  height: 25%;
}

.Intro{
  min-width: 100svw;
  min-height: 101svh;
  scale: 1;
  z-index: 10000;
}

.Intro-disappear{
  animation: hideIntro .8s ease forwards 0.5s,
  displaynone 0s ease forwards 2s;
}

@keyframes hideIntro {
  to{
      scale: 0;
  }
}

@keyframes displaynone {
  to{
      display: none;
  }
}

.primordial-choice{
  width: 100%;
  height: 100%;
  min-height: 100%;
  min-width: 100%;
  z-index: 900;
  /* background-color: rgb(255, 255, 255); */
  background-color: transparent;
  text-align: center;
  z-index: 10000;
}

.search{
  position: relative;
  top: 10%; 
}

.content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
}

.block-choice{
  width: 100%;
  height: 70svh;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: center;
  gap: 10vh;
  margin-top: 5vh;
}

.content{
  scale: 1;
}

.content-disappear{
  animation: hideIntro .8s ease forwards 0.5s,
  displaynone 0s ease forwards 2s;
}


