/* Animation cube categories */

/* Pierres */

.cube-pierres {
  width: 30vh;
  height: 30vh;
  position: relative;
  transform-style: preserve-3d;
  animation: rotationtoleft 5s infinite linear;
}

.cube-pierres div {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 1;
}

.front-p {
  transform: translateZ(15vh);
  background: pink;
}

.back-p {
  transform: translateZ(-15vh);
  background: rgb(156, 65, 115);
}

.right-p {
  transform: rotateY(90deg) translateZ(15vh);
  background-image: url("../public/Pierre.jpg");
  background-size: cover;
}

.left-p {
  transform: rotateY(-90deg) translateZ(15vh);
  background-image: url("../public/Pierre.jpg");
  background-size: cover;
}

.top-p {
  transform: rotateX(90deg) translateZ(15vh);
  background: rgb(0, 62, 128);
}

.bottom-p {
  transform: rotateX(-90deg) translateZ(15vh);
  background: rgb(115, 173, 239);
}

@keyframes rotationtoleft {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

/* Encens */

.cube-encens {
  width: 30vh;
  height: 30vh;
  position: relative;
  display: flex;
  transform-style: preserve-3d;
  animation: rotationtoright 5s infinite linear;
  justify-content: center;
  flex-wrap: nowrap;
}

.cube-encens div {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 1;
}

.front-e {
  transform: translateZ(15vh);
  background: rgb(170, 233, 140)
}

.back-e {
  transform: translateZ(-15vh);
  background: green;
}

.right-e {
  transform: rotateY(90deg) translateZ(15vh);
  background-image: url("../public/Encens.jpg");
  background-size: cover;
}

.left-e {
  transform: rotateY(-90deg) translateZ(15vh);
  background-image: url("../public/Encens.jpg");
  background-size: cover;
}

.top-e {
  transform: rotateX(90deg) translateZ(15vh);
  background: purple;
}

.bottom-e {
  transform: rotateX(-90deg) translateZ(15vh);
  background: pink;
}

@keyframes rotationtoright {
  from {
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
}

.cube-pierres, .cube-encens :hover{
  cursor: pointer;
}

