@keyframes moveInCircle {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes moveVertical {
  0% {
    transform: translateY(-50%);
  }
  50% {
    transform: translateY(50%);
  }
  100% {
    transform: translateY(-50%);
  }
}
@keyframes moveHorizontal {
  0% {
    transform: translateX(-50%) translateY(-10%);
  }
  50% {
    transform: translateX(50%) translateY(10%);
  }
  100% {
    transform: translateX(-50%) translateY(-10%);
  }
}

@keyframes roundtopleft {
  to{ border-top-left-radius: 50%;}
}

@keyframes roundtopright {
  to{ border-top-right-radius: 50%;}
}

@keyframes roundbottomleft {
  to{ border-bottom-left-radius: 50%;}
}

@keyframes roundbottomright {
  to{ border-bottom-right-radius: 50%;}
}




.gradient-bg {
  width: 100svw;
  height: 100svh;
  position: absolute;
  top: 0;
  overflow: hidden;
  background: linear-gradient(40deg, #6c00a2, #001152);
  top: 0;
  left: 0;
  z-index: 5000;
  padding: 0;
  opacity: 0.8;
}
.gradient-bg svg {
  position: fixed;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
}
.gradient-bg .gradients-container {
  filter: url(#goo) blur(40px);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}
.gradient-bg .g1 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(18, 113, 255, 0.8) 0%, rgba(18, 113, 255, 0.8) 50%) no-repeat;
  mix-blend-mode: hard-light;
  width: 80%;
  height: 80%;
  top: calc(50% - 80% / 2);
  left: calc(50% - 80% / 2);
  transform-origin: center center;
  animation: moveVertical 30s ease infinite;
  opacity: 1;
  /* animation: roundtopleft 10s ease 1s infinite, roundbottomright 10s ease-in-out 3s, roundtopright 10s ease-in-out 7s; */
}
.gradient-bg .g2 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(221, 74, 255, 0.8) 0, rgba(221, 74, 255, 0.8) 50%) no-repeat;
  mix-blend-mode: hard-light;
  width: 80%;
  height: 80%;
  top: calc(50% - 80% / 2);
  left: calc(50% - 80% / 2);
  transform-origin: calc(50% - 400px);
  animation: moveInCircle 20s reverse infinite, roundtopleft 20s ease 1s infinite, roundbottomright 15s ease 3s infinite, roundtopright 18s ease-in-out 7s infinite, roundbottomleft 20s ease 3s infinite;
  opacity: 1;
}
.gradient-bg .g3 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(100, 220, 255, 0.8) 0, rgba(100, 220, 255, 0.8) 50%) no-repeat;
  mix-blend-mode: hard-light;
  width: 80%;
  height: 80%;
  top: calc(50% - 80% / 2 + 200px);
  left: calc(50% - 80% / 2 - 500px);
  transform-origin: calc(50% + 400px);
  animation: moveInCircle 40s linear infinite, roundtopleft 20s ease 1s infinite, roundbottomright 15s ease 3s infinite, roundtopright 18s ease-in-out 7s infinite, roundbottomleft 20s ease 3s infinite;
  opacity: 1;
}
.gradient-bg .g4 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(200, 50, 50, 0.8) 0, rgba(200, 50, 50, 0.8) 50%) no-repeat;
  mix-blend-mode: hard-light;
  width: 80%;
  height: 80%;
  top: calc(50% - 80% / 2);
  left: calc(50% - 80% / 2);
  transform-origin: calc(50% - 200px);
  animation: moveHorizontal 40s ease infinite, roundtopleft 20s ease 1s infinite, roundbottomright 15s ease 3s infinite, roundtopright 18s ease-in-out 7s infinite, roundbottomleft 20s ease 3s infinite;
  opacity: 0.7;
}
.gradient-bg .g5 {
  position: absolute;
  background: radial-gradient(circle at center, rgba(180, 180, 50, 0.8) 0, rgba(180, 180, 50, 0.8) 50%) no-repeat;
  mix-blend-mode: hard-light;
  width: calc(80% * 2);
  height: calc(80% * 2);
  top: calc(50% - 80%);
  left: calc(50% - 80%);
  transform-origin: calc(50% - 800px) calc(50% + 200px);
  animation: moveInCircle 40s ease infinite, roundtopleft 20s ease 1s infinite, roundbottomright 15s ease 3s infinite, roundtopright 18s ease-in-out 7s infinite, roundbottomleft 20s ease 3s infinite;
  opacity: 1;
}
.gradient-bg .interactive {
  position: absolute;
  background: radial-gradient(circle at center, rgba(140, 100, 255, 0.8) 0, rgba(140, 100, 255, 0.8) 50%) no-repeat;
  mix-blend-mode: hard-light;
  width: 100%;
  height: 100%;
  top: -50%;
  left: -50%;
  opacity: 0.7;
}

