.rockBlock{
    position: absolute;
    top: 15%;
    /* top: 20%; */
    width: 100%;
    height: 85%;
    /* background-color: rgb(255, 255, 255); */
    background-color: transparent;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 1vh;
    z-index: 10000;
}

/* .rockBlock h2{
    font-size: clamp(0.8rem, 1.3rem, 1.5rem);
} */

.rockBlock p{
    font-size: clamp(0.7rem, 1rem, 1.5rem);
}
/* Barre de recherche */

.searchBar{
    padding: 2rem 1rem;
}

.searchBarBlock{
    height: 1.7rem;
    width: auto;
    display: flex;
}
.searchBarBlock input {
    font-size: 1.2rem;
    border-top-left-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
}

#validationButton {
    width: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-right-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
    background: white;
}

#validationButton img{
    width: 100%;
    height: 100%;
}

/* Slider */
.blockCarousel{
    max-width: 54vh;
    position: relative;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    /* max-width: 70vh; */
}

.carousel-root {
    padding: 0.5rem;
    border: 1px solid salmon;
    border-radius: 1rem;
    /* height: 70svh; */
    width: 86%;
    /* align-self: center; */
    margin: 0 auto;
    height: 100%;
}

.carousel {
    max-height: 50vh;
    max-width: 50vh;
    display: flex;
    justify-content: center;
}

.carousel .slider {
    max-height: 46vh;
}

.carousel .slider-wrapper {
    overflow: hidden;
    margin: 0 !important;
    /* width: 86%; */
    transition: height .15s ease-in;
}

.carousel .slide {
    padding: 0.1rem;
}

.thumbs-wrapper{
    margin-top: -.5rem !important;
}

.control-arrow {
    display: none;
}

.control-dots {
    margin: 0 !important;
    position: absolute;
    /* top: 46vh; */
}

.dot{
    transition: opacity .25s ease-in;
    opacity: .3;
    filter: alpha(opacity = 30);
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 1) !important;
    background: #8febbf !important;
    border-radius: 50%;
    width: 8px;
    height: 8px;
    cursor: pointer;
    display: inline-block;
    margin: 0 8px;
}

.blockOverlay{
    display: flex;
    flex-flow: column nowrap ;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    top: 0;
    width: 100%;
    /* max-height: 50vh;
    min-height: 50vh; */
    /* background: rgba(128, 128, 128, 0.4); */
    background: transparent;
    border-radius: 1rem;
    /* border: 1px solid green; */

}

.selected>.blockOverlay>img {
    border: 2px solid green !important;
    border-radius: 1rem;
}

.overlay {
    display: flex;
    flex-flow: column nowrap ;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    top: -8vh;
    width: 100%;
    max-height: 4vh;
    background: rgba(128, 128, 128, 0.4);
}

.overlayO{
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    top: 0;
    width: 100%;
    background: rgba(200, 70, 70, 0.4);
    min-height: 100%;
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
}

.overlayTitle {
    color: black;
    text-transform: uppercase;
    font-size: clamp(0.8rem, 1.3rem, 1.5rem);
    margin-top: 1%;
}

.overlayTitleO {
    display: none;
}

/* Fiche overlay quand photo clicker */


.gridOverlay {
    /* background: rgba(red, 0.4); */
    min-height: 50vh;
    width: 100%;
    grid-column: 1 / -1;
    grid-row: 1 / -1;
    position: relative;
    display: grid;
    justify-items: center;
    align-items: center;
    transform: translateY(100%);
    transition: all 0.3s ease-in-out;
}

.gridOverlay button {
    background: none;
    outline: none;
    font-weight: 100;
    letter-spacing: 2px;
    border: 1px solid white;
    color: white;
    text-transform: uppercase;
    padding: 10px;
    margin-top: -10rem;
}

.hoverO{
    background: none;
    outline: none;
    font-weight: 100;
    letter-spacing: 3px;
    /* border: 1px solid white; */
    color: white;
    text-transform: uppercase;
    /* padding: 10px; */
    transform: translateY(2%);
    }




/* Fin de Fiche overlay quand photo clicker */

/* .thumbs {
    display: flex !important;
    justify-content: center !important;
} */

.thumbs-wrapper{
    display: flex !important;
    justify-content: center !important;
    margin: 1rem 0 0 -2rem !important;
}
/* .axis-vertical {
    display: flex;
    justify-content: center;
} */

.carousel-status{
    display: none;
}

#canvas{
    display: block;
    position: absolute;
    top: 0;
    left: 0;
}

.carousel .thumbs {
    transition: all .15s ease-in;
    transform: translate3d(0, 0, 0);
    position: relative;
    list-style: none;
    white-space: wrap;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.carousel .carousel-slider {
    min-height: 50svh;
    max-height: 50svh;
}

